import React from "react";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faFacebook, faGithub, faInstagram} from "@fortawesome/free-brands-svg-icons";
import {Link} from 'react-router-dom';
import Typed from 'react-typed';

const HomePage = () => {
  return (
    <div className="HomePage">
      <header className="intro">
        <h1 className="intro-text">
          Hi! I'm{" "}
          <span>
            <Typed
              className="typed-text"
              strings={[
                "Myilvaganan",
                "a Farmer",
                "a Web Developer",
                "Myilvaganan",
                "a Frontend Developer",
                "a Backend Developer",
                "Myilvaganan",
                "a MERN Developer",
                "a Nature Lover!",
              ]}
              typeSpeed={80}
              backSpeed={80}
              loop
            />
          </span>
        </h1>
        <p className="intro-text-para">
          Full-stack development/ front-end development position emphasizing
          strong web UI (react.js, node.js, express.js, JavaScript, html5,
          css3,etc.) And general full-stack development skills including Restful API's and databases. Bring to the team a solid track record of success in the delivery of software applications, hands-on experience, and diverse industry background. Equally strong in the front-end web UI, middle-tier, and back-end technologies.
        </p>
        <div className="icons">
          <Link className="icon-holder">
            <FontAwesomeIcon className="icon fb" icon={faFacebook} />
          </Link>
          <Link className="icon-holder">
            <FontAwesomeIcon className="icon gh" icon={faGithub} />
          </Link>
          <Link className="icon-holder">
            <FontAwesomeIcon className="icon ig" icon={faInstagram} />
          </Link>
        </div>
      </header>
    </div>
  );
};

export default HomePage;
